.syncPanel {
	width: 100%;
	height: 100%;
}

.navigationWrapper {
	float: right;
}
.navigationItem {
	height: 10px;
	padding-right: 30px;
	display: table-cell;
	vertical-align: middle;
}

.toolbarIcon {
	background-repeat: no-repeat;
	width: 35px;
	height: 40px;
	background-size: contain;
	background-position: center;
}
.number {
	color: #dcdcdc;
	font-size: 12px;
	margin-top: 5px;
	margin-left: 30px;
	position: absolute;
	font-family: Inconsolata;
}

.errorList {
	list-style: decimal;
	font-family: Inconsolata;
	font-size: 14px;
	padding-left: 5px;
}

.disabledNavigationItem {
	pointer-events: none;
	opacity: 0.2;
}

.fileList {
	list-style: none;
}

.li {
	color: #dcdcdc;
	font-family: Inconsolata;
	padding: 8px;
	padding-left: 13px;
	cursor: pointer;
}

li:hover {
	background-color: #b9bfbb;
	transition: 200ms all;
	transform: scale(1.05);
	word-wrap: break-word;
	cursor: pointer;
}


.liActive {
	background-color: #b9bfbb;
	transform: scale(1.05);
	cursor: pointer;
}

.codeViewer {
	background-color: white;
	margin: 5px;
	overflow: scroll;
	height: calc(100% - 40px);
	cursor: text;
}

.codeTextEditor {
	background-color: #f6f6f6;
	margin: 15px;
	min-width: calc(100% - 40px);
	min-height: calc(100% - 70px);
	position: absolute;
	font-family: Inconsolata;
	font-size: 16px;
	outline: none;
	resize: none;
	border: none;
}

.codeEditNav {
	margin-left: 20px;
}

.codeEditNavItem {
	display: table-cell;
	height: 20px;
	font-weight: bold;
	color: #3CB8FE;
	text-transform: uppercase;
	font-family: Inconsolata;
	padding-right: 30px;
	vertical-align: middle;
	cursor: pointer;
	user-select: none;
}

.codeEditNavItem:hover {
	transition: 200ms all;
	transform: scale(1.05);
}

.panelWrapper {
	text-align: left;
}


h1, h2, h3 {
	font-family: Inconsolata;
	color: #dcdcdc;
	padding-bottom: 10px;
}
h1 {
	font-size: 50px;
}

h2 {
	font-size: 30px;
}

h3 {
	font-size: 15px;
}
