.Difference {
    font-family: Inconsolata;
    white-space: pre;
    width: 100%;
    margin: 0px;
    padding: 0px;
    min-width: 100%;
}

.Difference > del {
    background-color: rgb(255, 224, 224);
    text-decoration: none;
    display: block;
    min-width: 100%;
}

.Difference > ins {
    background-color: rgb(201, 238, 211);
    text-decoration: none;
    display: block;
    min-width: 100%;
}

.Difference > same {
    display: block;
    white-space: pre;
    min-width: 100%;
}