body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.syncPanel {
	width: 100%;
	height: 100%;
}

.navigationWrapper {
	float: right;
}
.navigationItem {
	height: 10px;
	padding-right: 30px;
	display: table-cell;
	vertical-align: middle;
}

.toolbarIcon {
	background-repeat: no-repeat;
	width: 35px;
	height: 40px;
	background-size: contain;
	background-position: center;
}
.number {
	color: #dcdcdc;
	font-size: 12px;
	margin-top: 5px;
	margin-left: 30px;
	position: absolute;
	font-family: Inconsolata;
}

.errorList {
	list-style: decimal;
	font-family: Inconsolata;
	font-size: 14px;
	padding-left: 5px;
}

.disabledNavigationItem {
	pointer-events: none;
	opacity: 0.2;
}

.fileList {
	list-style: none;
}

.li {
	color: #dcdcdc;
	font-family: Inconsolata;
	padding: 8px;
	padding-left: 13px;
	cursor: pointer;
}

li:hover {
	background-color: #b9bfbb;
	-webkit-transition: 200ms all;
	transition: 200ms all;
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
	word-wrap: break-word;
	cursor: pointer;
}


.liActive {
	background-color: #b9bfbb;
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
	cursor: pointer;
}

.codeViewer {
	background-color: white;
	margin: 5px;
	overflow: scroll;
	height: calc(100% - 40px);
	cursor: text;
}

.codeTextEditor {
	background-color: #f6f6f6;
	margin: 15px;
	min-width: calc(100% - 40px);
	min-height: calc(100% - 70px);
	position: absolute;
	font-family: Inconsolata;
	font-size: 16px;
	outline: none;
	resize: none;
	border: none;
}

.codeEditNav {
	margin-left: 20px;
}

.codeEditNavItem {
	display: table-cell;
	height: 20px;
	font-weight: bold;
	color: #3CB8FE;
	text-transform: uppercase;
	font-family: Inconsolata;
	padding-right: 30px;
	vertical-align: middle;
	cursor: pointer;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.codeEditNavItem:hover {
	-webkit-transition: 200ms all;
	transition: 200ms all;
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}

.panelWrapper {
	text-align: left;
}


h1, h2, h3 {
	font-family: Inconsolata;
	color: #dcdcdc;
	padding-bottom: 10px;
}
h1 {
	font-size: 50px;
}

h2 {
	font-size: 30px;
}

h3 {
	font-size: 15px;
}

.Difference {
    font-family: Inconsolata;
    white-space: pre;
    width: 100%;
    margin: 0px;
    padding: 0px;
    min-width: 100%;
}

.Difference > del {
    background-color: rgb(255, 224, 224);
    text-decoration: none;
    display: block;
    min-width: 100%;
}

.Difference > ins {
    background-color: rgb(201, 238, 211);
    text-decoration: none;
    display: block;
    min-width: 100%;
}

.Difference > same {
    display: block;
    white-space: pre;
    min-width: 100%;
}
